.statistics{
    width: 500px;
        margin-top: 70px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 70px;
        box-shadow: 5px 5px 15px 5px grey;
}

.title{
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
    text-transform: uppercase;
    color: gray;
    justify-content: center;
        align-items: center;
        margin: 0;

}
.statList {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style: none;
}

.item {
    width: 100px;
    text-align: center;
    height: 90px;
}

.label {
    display: block;
    padding-bottom: 10px;
    margin-top:15px ;
}

.percentage {
    display: block;
   
}